<template>
  <div class="comment-box">
    <div class="comment" v-for="c in comments" :key="c.id">
      <p><b>{{ c.writer.nickname }}</b> {{ c.comment }}</p>
    </div>
    <form class="comment-form">
      <input ref="written-comment-input" type="text" placeholder="댓글을 작성해 보세요."/>
      <button type="button" @click="submitComment">Submit</button>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";

export default {
  name: "CommentBox",
  data() {
    return {
      writtenComment: {},
    };
  },
  props: {
    comments: Array,
    postId: Number,
  },
  methods: {
    async submitComment() {
      let writtenContent = this.$refs["written-comment-input"].value;
      this.writtenComment.comment = writtenContent;
      this.writtenComment.snsId = this.postId;

      const response = await axios.post(apiURL + '/comment/write', this.writtenComment, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        }
      });
      console.log(response);
      this.$emit('comment-posted', this.writtenComment);
      writtenContent = ''; // 댓글 작성 후 입력 필드 초기화
    },
  },
};
</script>

<style scoped>
.comment-box {
  margin-top: 24px;
  border-top: 1px solid #ccc;
  padding-top: 16px;
}

.comment {
  margin-bottom: 16px;
}

.comment-form {
  display: flex;
}

input[type="text"] {
  flex-grow: 1;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  margin-right: 8px;
}

button {
  background-color: #42b983;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: bold;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #2c3e50;
}
</style>
